<template>
	<view class="countdown">
		<view class="countdown-item">
			<view class="countdown-item-label">Days</view>
			<view class="countdown-item-value">{{ days }}</view>
		</view>
		<view class="countdown-item">
			<view class="countdown-item-label">Hours</view>
			<view class="countdown-item-value">{{ hours }}</view>
		</view>
		<view class="countdown-item">
			<view class="countdown-item-label">Minutes</view>
			<view class="countdown-item-value">{{ minutes }}</view>
		</view>
		<view class="countdown-item">
			<view class="countdown-item-label">Seconds</view>
			<view class="countdown-item-value">{{ seconds }}</view>
		</view>
	</view>
</template>

<script>
	export default {
		name: "Countdown",
		props: {
			endTime: {
				type: Number,
				required: true,
			},
		},
		data() {
			return {
				days: "00",
				hours: "00",
				minutes: "00",
				seconds: "00",
				timer: null,
			};
		},
		created() {
			this.countdown();
		},
		beforeDestroy() {
			clearInterval(this.timer);
		},
		methods: {
			countdown() {
				this.timer = setInterval(() => {
					const diff = this.endTime - Math.floor(Date.now() / 1000);

					if (diff <= 0) {
						clearInterval(this.timer);
						return;
					}

					this.days = this.formatNumber(Math.floor(diff / (60 * 60 * 24)));
					this.hours = this.formatNumber(
						Math.floor((diff % (60 * 60 * 24)) / (60 * 60))
					);
					this.minutes = this.formatNumber(
						Math.floor((diff % (60 * 60)) / 60)
					);
					this.seconds = this.formatNumber(diff % 60);
				}, 1000);
			},
			formatNumber(number) {
				return number < 10 ? `0${number}` : `${number}`;
			},
		},
	};
</script>

<style scoped>
	.countdown {
		display: flex;
		justify-content: center;
		align-items: center;
	}

	.countdown-item {
		margin-right: 20px;
	}

	.countdown-item:last-child {
		margin-right: 0;
	}

	.countdown-item-label {
		font-size: 14px;
		color: #999;
		margin-bottom: 5px;
	}

	.countdown-item-value {
		font-size: 18px;
		font-weight: bold;
		color: #ff4545;
	}
</style>
